:root {
  --color-black: #000;
  --color-white: #fff;
  --color-black-brown: #232323;
  --color-gray-brown: #353535;
  --color-wet-asphalt: #535353;
  --color-mandarin-tango: #e55047;
  --color-white-aluminum: #a5a5a5;
  --color-gainsborough: #dbdbdb;
  --color-smoky-white: #f9f9f9;
}

@font-face {
  font-family: "Bitter";
  src: url("../fonts/Bitter-Bold.woff2") format("woff2"), url("../Bitter-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Bitter";
  src: url("../fonts/Bitter-Medium.woff2") format("woff2"), url("../Bitter-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Bitter";
  src: url("../fonts/Bitter-Regular.woff2") format("woff2"), url("../Bitter-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

